import React, {useEffect, useState} from 'react'
import { Link, useNavigate } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
  CSmartTable,
  CBadge
} from '@coreui/react-pro'
import CIcon from '@coreui/icons-react'
import { cilDelete, cilEyedropper, cilLockLocked, cilLowVision, cilPen, cilProduct, cilViewColumn, cilViewModule, cilViewQuilt } from '@coreui/icons'
import axios from 'axios'
import { Role } from 'src/constant'

const Products = () => {
  const navigate = useNavigate();
  const [products, setProducts] = useState([])
  const [role, setRole] = useState('');

  useEffect(() => {
    setRole(localStorage.getItem('role'));
    axios.get('product/findAll', {headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }}).then(response => setProducts(response.data.data))
    .catch(error => console.error('error fetching products', error));
  }, [])

  const deleteProductAction = (e, productId) => {
    e.preventDefault();
    axios.delete('/product/delete/'+productId, {headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }} )
        .then((r) => {
          window.location.reload();
        })
        .catch((e) => {
            console.log(e.response.data.errors);
        });
  }

  const columns = [
    {
      key: 'name',
      label: 'Name',
      filter: false,
    },
    {
      key: 'isActive',
      label: 'Published',
      filter: false,
      sorter:false,
    },
    {
      key: 'client',
      label: 'Company',
      filter: false,
    },
    {
      key: 'actions',
      label: 'Actions',
      filter: false,
      sorter: false,
      _style: { width: '20%' }

    },
  ]

  return (
    <>
    <div className="bg-body-tertiary min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
        <CCol xl={12}>
          <CCard className="mb-4">
            <CCardBody className="p-4">
        <CRow className='p-4'>
          <CCol sm={5}><h4>Products</h4></CCol>

          <CCol sm={7} className='d-none d-md-block'>
          { role != Role.ClientViewOnly?
          <Link to="/addProduct">
            <CButton className='btn btn-primary float-end' type='button'>Add New Product</CButton>
          </Link>: ''}
          {/* <Link to="/importProducts">
          <CButton className='btn btn-primary float-end' type='button'>Import Products</CButton>
          </Link> */}
          </CCol>
        </CRow>
              <CCol md={12}>
              <CSmartTable
        activePage={2}
        cleaner
        clickableRows
        columns={columns}
        columnFilter
        columnSorter
        items={products}
        itemsPerPageSelect
        itemsPerPage={5}
        pagination
        onFilteredItemsChange={(items) => {
          console.log(items)
        }}
        onSelectedItemsChange={(items) => {
          console.log(items)
        }}
        scopedColumns={{
          isActive: (item) => (
            <td>
              {item.isActive? 'Published': 'Unpublished'}
            </td>
          ),
          client: (item) => (
            <td>
              {(item.client !== null)? item.client.companyName: 'N/A'}
            </td>
          ),    
          actions: (item) => (
            <td>
              <div onClick={(e) => navigate("/editProduct", {state:{productId: item.id}})}>
                <CIcon icon={cilPen} className="me-2" />
              </div>
              { role != Role.ClientViewOnly?
                <CIcon icon={cilDelete} className="me-2" onClick={(e)=>deleteProductAction(e, item.id)} />: ''}
            </td>
          ),
        }}    
        selectable
        sorterValue={{ column: 'createdAt', state: 'desc' }}
        tableFilter
        tableProps={{
          className: 'add-this-class',
          responsive: true,
          striped: true,
          hover: true,
        }}
        tableBodyProps={{
          className: 'align-middle'
        }}
      />
              </CCol>
            </CCardBody>
          </CCard>
        </CCol>
        </CRow>
      </CContainer>
    </div>
    </>
  )
}

export default Products
